'use client';
import Link from 'next/link';
import { useState } from 'react';
import Image from 'next/image';
import logo from '../../public/assets/logo.png';
const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(true);

  const handleToggle = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div
      className={`${
        isOpen ? 'w-64' : 'w-0'
      } bg-gray-800 text-white p-4 fixed top-0 left-0 bottom-0 z-10 transition-width duration-300`}
    >
      <div className="logo mb-4">
        <Image src={logo} alt="Logo" className="w-72 h-54 mx-auto pointer-events-none"  />
      </div>
      <ul>
        <li>
          <Link href="/admin/dashboard" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-tachometer-alt mr-2" />
            {isOpen && <span>Dashboard</span>}
          </Link>
        </li>
        <li>
          <Link href="/admin/orders" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-shopping-cart mr-2" />
            {isOpen && <span>Orders</span>}
          </Link>
        </li>
        <li>
          <Link href="/category" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-list mr-2" />
            {isOpen && <span>Category</span>}
          </Link>
        </li>
        <li>
          <Link href="/sellers" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-user mr-2" />
            {isOpen && <span>Sellers</span>}
          </Link>
        </li>
        <li>
          <Link href="/payment-request" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-money-bill-alt mr-2" />
            {isOpen && <span>Payment Request</span>}
          </Link>
        </li>
        <li>
          <Link href="/deactive-sellers" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-user-slash mr-2" />
            {isOpen && <span>Deactive Sellers</span>}
          </Link>
        </li>
        <li>
          <Link href="/seller-request" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-user-plus mr-2" />
            {isOpen && <span>Seller Request</span>}
          </Link>
        </li>
        <li>
          <Link href="/live-chat" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-comment mr-2" />
            {isOpen && <span>Live Chat</span>}
          </Link>
        </li>
        <li>
          <Link href="/logout" className="flex items-center py-2 pl-4 hover:bg-gray-700">
            <i className="fas fa-sign-out-alt mr-2" />
            {isOpen && <span>Logout</span>}
          </Link>
        </li>
      </ul>
      <button
        className="toggle-button absolute top-4 right-4"
        onClick={handleToggle}
      >
        <i className="fas fa-bars" />
      </button>
    </div>
  );
};

export default Sidebar;
